<template>
  <div id="app">
    <Prompt v-if="type == 'prompt'"></Prompt>
    <Download v-if="type == 'download'"></Download>
  </div>
</template>
<script>
import Prompt from 'views/prompt/Prompt';
import Download from 'views/download/Download';

export default {
  name: 'App',
  components: {
    Prompt,
    Download,
  },
  data() {
      return {
        type: ''
      }
  },
  mounted() {
    var wx = (function() {
      return navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1;
    })();
    if (wx) {
      this.type = 'prompt';
    } else {
      this.type = 'download';
    }
  },
};
</script>
<style>
html {
  overflow: hidden;
}
</style>
